@use '/src/styles/constants/colors';
@use '/src/styles/mixins/responsive';

.wrapper {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  align-items: center;
  width: 70px;
  padding: 5px;
  margin-right: 5px;
  border: 1px solid colors.$GRAY3;
  border-radius: 10px;

  &:last-child {
    margin-right: 0;
  }

  .temp {
    margin-bottom: 5px;
    font-size: 12px;
  }

  .iconBox {
    margin-bottom: 5px;

    img {
      width: 40px;
      height: 40px;
      object-fit: contain;
    }
  }

  .date {
    font-size: 12px;
    color: colors.$GRAY;
  }

  @include responsive.after(SD) {
    width: 100px;
    margin-right: 10px;

    .temp,
    .date {
      font-size: 16px;
    }

    .iconBox {
      img {
        width: 60px;
        height: 60px;
      }
    }
  }
}
